import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import Container from "@mui/material/Container";
import React from "react";
import FormatListBulletedIcon from "@mui/icons-material/FormatListBulleted";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import makeStyles from "@mui/styles/makeStyles";
import {
  Banner,
  SearchBar,
  CardImg,
  ProfileBar,
  BottomNav,
  TopNav,
} from "../../components";
import { useNavigate } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import {
  getDokterList,
  getPoliList,
  setDetailDoctor,
} from "../../redux/actions";
import { connect } from "react-redux";
import doctorDefault from "../../assets/doctors.png";
import { BASE_URL_IMG } from "../../helpers/common/config";

const useStyles = makeStyles(() => ({
  textContent: {
    padding: "0 8px !important",
    flexGrow: 100,
  },
  filterContent: {
    flexGrow: 100,
  },
  searchBar: {
    display: "flex",
  },
  root: {
    paddingTop: "16px",
    paddingBottom: "16vh",
  },
}));

const DocterListPage = (props) => {
  const {
    actGetDokterList,
    stateDoctorList,
    statePoliList,
    actGetPoliList,
    actSetDetailDoctor,
    stateBahasa,
  } = props;
  const classes = useStyles();
  const navigate = useNavigate();

  const [namaDokter, setNamaDokter] = React.useState("");

  const handleSubmitSearch = () => {
    actGetDokterList({
      namaDokter,
      layananId: "",
    });
  };
  React.useEffect(() => {
    actGetDokterList({
      namaDokter: "",
      layananId: "",
    });
    actGetPoliList();
  }, []);

  const handleClickLayanan = (id) => {
    actGetDokterList({
      namaDokter,
      layananId: id,
    });
  };
  return (
    <Container className={classes.root}>
      <Stack spacing={2}>
        <TopNav
          type="back"
          title={stateBahasa === "indonesia" ? "Dokter" : "Doctors List"}
          variant="with-profile"
          click={() => {
            navigate(-1);
          }}
        />
        <div className={classes.searchBar}>
          <SearchBar
            label={stateBahasa === "indonesia" ? "Cari Dokter" : "Find Doctor"}
            onChange={(el) => setNamaDokter(el.target.value)}
            value={namaDokter}
            onSubmit={handleSubmitSearch}
          />
          {/* <Paper>
            <IconButton>
              <FormatListBulletedIcon />
            </IconButton>
          </Paper> */}
        </div>
        <Swiper
          // pagination={true}
          slidesPerView={1.1}
          spaceBetween={10}
          className="mySwiper"
        >
          {[1, 2, 3, 4].map((item, index) => {
            return (
              <SwiperSlide key={"carousel_" + index}>
                <Banner />
              </SwiperSlide>
            );
          })}
        </Swiper>
        <Swiper
          // pagination={true}
          slidesPerView={4}
          spaceBetween={10}
          className="mySwiper"
        >
          {statePoliList &&
            statePoliList.map((item, index) => {
              let title = item.ref_layanan_nama.substring(0, 12);
              return (
                <SwiperSlide key={"poli_" + index}>
                  <CardImg
                    type="small"
                    title={title}
                    text="Bedah Hati"
                    click={() => handleClickLayanan(item.id_poli)}
                  />
                </SwiperSlide>
                // <Grid item xs={3}>
                //   <CardImg
                //     type="small"
                //     title="Dr Qahtan"
                //     text="Bedah Hati"
                //     click={() => navigate("/make-appointment")}
                //   />
                // </Grid>
              );
            })}
        </Swiper>
        <Grid container>
          <Grid item xs={12}>
            <Typography variant="subtitle2">
              {stateBahasa === "indonesia" ? "Dokter Kami" : "Our Doctors"}
            </Typography>
          </Grid>
          <Grid container item xs={12} spacing={2}>
            {stateDoctorList &&
              stateDoctorList.map((item, index) => {
                return (
                  <Grid item xs={6} md={4} key={"dokter" + index}>
                    <CardImg
                      type="medium"
                      title={item.nama_dokter}
                      text={item.ref_layanan_nama}
                      img={
                        item.filename
                          ? BASE_URL_IMG + "/" + item.filename
                          : doctorDefault
                      }
                      click={() =>
                        actSetDetailDoctor(item.peg_id, () =>
                          navigate(`/doctor-detail/${item.peg_id}`)
                        )
                      }
                    />
                  </Grid>
                );
              })}
          </Grid>
        </Grid>
      </Stack>
      <BottomNav />
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    stateDoctorList: state.master.doctorList,
    statePoliList: state.master.poliList,
    stateBahasa: state.app.bahasa,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actSetDetailDoctor: (body, action) =>
      dispatch(setDetailDoctor(body, action)),
    actGetDokterList: (body, action) => dispatch(getDokterList(body, action)),
    actGetPoliList: (body, action) => dispatch(getPoliList(body, action)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(DocterListPage);
